@font-face {
  font-family: 'starHollow';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

:root {
  --base: #000000;
  --text: #ffffff;
  --cream: #ffffc8;
  --yellow: #ffe81f;
}

html {
  height: 100%;
}
body {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  color: var(--text);
  background-color: var(--base);
  font-family: 'star';
  overflow: hidden;
}

button {
  cursor: pointer;
}

#space {
  z-index: -99;
  position: absolute;
  top: 0;
  left: 0;
  background: var(--base);
}

#landingPage {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
#landingPage button {
  z-index: 99;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 4px 16px;
  font-family: 'star';
  font-size: 3rem;
  color: var(--cream);
  background: none;
  border: none;
}
#landingPage button:hover { color: var(--text); }
#landingPage button:active { color: var(--cream); }

#mainPage {
  z-index: 99;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

#mainPage .socials {
  z-index: 99;
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 48px);
  margin-top: 32px;
  margin-left: 24px;
  margin-right: 24px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px; 
}
#mainPage .socials button {
  font-family: 'star';
  font-size: 1rem;
  letter-spacing: 1px;
  color: var(--text);
  background: none;
}
#mainPage .socials button:hover { color: var(--cream); }
#mainPage .socials button:active { color: var(--text); }
#mainPage .socials button:nth-child(1) {
  border-top: none;
  border-bottom: none;
  border-left: none;
  border-right: 1px solid var(--cream);
}
#mainPage .socials button:nth-child(2) {
  border-top: none;
  border-bottom: none;
  border-left: 1px solid var(--cream);
  border-right: 1px solid var(--cream);
}
#mainPage .socials button:nth-child(3) {
  border-top: none;
  border-bottom: none;
  border-left: 1px solid var(--cream);
  border-right: 1px solid var(--cream);
}
#mainPage .socials button:nth-child(4) {
  border-top: none;
  border-bottom: none;
  border-left: 1px solid var(--cream);
  border-right: none;
}

#mainPage .footer {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 48px);
  margin-top: 90px;
  margin-left: 24px;
  margin-right: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
}
#mainPage .footer span {
  font-size: 1rem;
  letter-spacing: 2px;
  word-break: break-all;
  text-align: start;
}
#mainPage .footer button {
  font-family: 'star';
  font-size: 1rem;
  letter-spacing: 1px;
  line-height: 20px;
  color: var(--cream);
  background: none;
  border: none;
}

/* Star Wars Scroll Text */
#sw_scroller {
  z-index: 0;
  height: 90em;
  width: 20em;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) perspective(320px) rotateX(24deg);
  transform-origin: 50% 100%;
  font-family: 'starHollow';
  font-weight: bold;
  color: var(--yellow);
  font-size: 350%;
  text-align: justify;
  overflow: hidden;
}
#sw_scroller::after {
  position: absolute;
  content: '';
  bottom: 60%;
  left: 0;
  right: 0;
  top: 0;
}

#sw_scroller .content {
  position: absolute;
  top: 100%;
  animation: sw_scroll 169s linear 2s;
}
@keyframes sw_scroll {
  0% {
    top: 100%;
  }
  100% {
    top: -200%;
  }
}

#sw_scroller .content .title, #sw_scroller .content .subtitle {
  line-height: 1.5rem;
} 
#sw_scroller .content .subtitle {
  text-transform: capitalize;
  font-size: 3rem;
}

@media screen and (max-width: 1000px) {
  #mainPage .socials {
    width: calc(100% - 32px);
    margin-top: 90px;
    margin-left: 16px;
    margin-right: 16px;
  }
  #mainPage .footer {
    width: calc(100% - 48px);
    margin-top: 32px;
    margin-left: 24px;
    margin-right: 24px;
    gap: 16px;
  }
  #mainPage .footer span {
    font-size: 0.8rem;
    letter-spacing: 1px;
  }
  #sw_scroller {
    height: 88em;
    width: 16em;
    font-size: 1.4rem;
  }
  #sw_scroller::after {
    position: absolute;
    content: '';
    bottom: 60%;
    left: 0;
    right: 0;
    top: 0;
  }
  #sw_scroller .content {
    animation: sw_scroll 148s linear 2s;
  }

  #sw_scroller .content .title, #sw_scroller .content .subtitle {
    line-height: 1.4rem;
  } 
  #sw_scroller .content .subtitle {
    text-transform: capitalize;
    font-size: 1.4rem;
  }
